.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); 
    display: flex;
    padding:5vw;
    justify-content: right;
    align-items: right;
  }
  
  .modal-content {
    background: rgb(232, 230, 226);
    padding: 20px;
    border-radius: 8px;
    width: 430px;
    max-width: 90%;
    
  }
  
  .modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 18px;
    cursor: pointer;
  }
  