.afternav {
    width: 99vw;
    height: 75vh;
    background-image: url('../../images/Rectangle 87.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    position: relative; /* Enable positioning for pseudo-element */
  }
  
  .afternav::before {
    content: ""; /* Empty content */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.35); /* Black with 35% transparency */
    z-index: 1; /* Place above the image (optional) */
  }
